import { FC, ReactNode } from 'react';

interface TabmenuProps {
  className?: string;
  items: { title: ReactNode; key: string; isActive: boolean; className?: string }[];
  onChange: (key: string) => void;
}

const TabMenu: FC<TabmenuProps> = ({ className, items, onChange }) => {
  return (
    <div className={`flex ${className}`}>
      {items.map((item) => {
        return (
          // Only NavLink Supports isActive styling https://reactrouter.com/docs/en/v6/components/nav-link
          <a
            className={`flex-grow cursor-pointer border-b-2 py-2 px-1 text-lg ${
              item.isActive
                ? 'border-indigo-500 text-indigo-500 dark:text-indigo-400'
                : 'border-gray-300 transition-colors duration-300 hover:border-indigo-400 dark:border-gray-800 hover:dark:border-indigo-600'
            } ${item.className ?? ''}`}
            key={item.key}
            onClick={() => {
              onChange(item.key);
            }}
          >
            {item.title}
          </a>
        );
      })}
    </div>
  );
};

export default TabMenu;
