import {
  AttributeDefinition,
  AttributeDefinitions,
  AttributeFile,
  SecurityGroupType,
} from '@youfoundation/transit-lib';
import { useState } from 'react';
import { t } from '../../../../helpers/i18n/dictionary';
import useAttribute from '../../../../hooks/profiles/useAttribute';
import { AttributeVm } from '../../../../hooks/profiles/useAttributes';
import ActionButton from '../../../Buttons/ActionButton';
import Select from '../../../Form/Select';
import Section from '../../../Sections/Section';
import AttributeFields from './AttributeFields';

const AttributeCreator = ({ profileId, sectionId }: { profileId: string; sectionId: string }) => {
  const [isActive, setIsActive] = useState(false);
  const [attribute, setAttribute] = useState<AttributeVm>();
  const [, { mutate: saveAttribute, isLoading, isError, isSuccess }] = useAttribute({});

  const setType = (typeId: string) => {
    const targetObj = Object.values(AttributeDefinitions).find(
      (curr) => curr.type.toString() === typeId
    ) as AttributeDefinition;

    setAttribute({
      id: '',
      type: typeId,
      sectionId: sectionId,
      priority: -1,
      data: {},
      typeDefinition: targetObj,
      profileId: profileId,
      acl: { requiredSecurityGroup: SecurityGroupType.Owner },
    });
  };

  const changeHandler: React.ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement> = (e) => {
    if (!attribute) {
      // attribute selection failed on type selection...
      return;
    }

    const newAttr: AttributeVm = { ...attribute };
    newAttr.data[e.target.name] = e.target.value;

    setAttribute(newAttr);
  };

  const saveNewData = () => {
    console.log(attribute);
    saveAttribute(attribute as AttributeFile, {
      onSuccess: () => {
        discard();
      },
    });
  };

  const discard = () => {
    setIsActive(false);
    setAttribute(undefined);
  };

  return (
    <>
      {isActive ? (
        <Section
          title={`New${attribute ? ':' : ''} ${attribute?.typeDefinition.name ?? ''}`}
          isOpaqueBg={true}
        >
          {attribute === undefined ? (
            <div className="mb-5">
              <label htmlFor="type">{t('Attribute Type')}</label>
              <Select
                id="type"
                onChange={(e) => {
                  setType(e.target.value);
                }}
              >
                <option>{t('Make a selection')}</option>
                {Object.values(AttributeDefinitions).map((def) => {
                  return (
                    <option value={def.type.toString()} key={def.type.toString()}>
                      {def.name}
                    </option>
                  );
                })}
              </Select>
            </div>
          ) : (
            <AttributeFields attribute={attribute} onChange={changeHandler} />
          )}
          <div className="flex flex-row">
            <ActionButton type="secondary" className="ml-auto" onClick={discard}>
              {t('Cancel')}
            </ActionButton>
            <ActionButton
              type="primary"
              icon="plus"
              className="ml-2"
              onClick={saveNewData}
              state={isLoading ? 'loading' : isSuccess ? 'success' : isError ? 'failed' : undefined}
            >
              {t('Add')}
            </ActionButton>
          </div>
        </Section>
      ) : (
        <div className="flex flex-row">
          <ActionButton
            type="primary"
            icon="plus"
            className="mx-auto min-w-[9rem]"
            onClick={() => setIsActive(true)}
          >
            {t('Add Attribute')}
          </ActionButton>
        </div>
      )}
    </>
  );
};

export default AttributeCreator;
