import { AttributeDefinitions } from '@youfoundation/transit-lib';
import { debounce } from 'lodash-es';
import { useMemo } from 'react';
import { t } from '../../../../helpers/i18n/dictionary';
import { AttributeVm } from '../../../../hooks/profiles/useAttributes';
import Input from '../../../Form/Input';
import Textarea from '../../../Form/Textarea';

const AttributeFields = ({
  attribute,
  onChange,
}: {
  attribute: AttributeVm;
  onChange: React.ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement>;
}) => {
  const debouncedChange = useMemo(() => debounce(onChange, 500), [onChange]);

  switch (attribute.type) {
    case AttributeDefinitions.Name.type.toString():
      return (
        <div className="-mx-2 flex flex-row">
          <div className="mb-5 w-2/5 px-2">
            <label htmlFor="given-name">{t('Given name')}</label>
            <Input
              id="given-name"
              name="givenName"
              defaultValue={attribute.data['givenName']}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5 w-3/5 px-2">
            <label htmlFor="sur-name">{t('Surname')}</label>
            <Input
              id="sur-name"
              name="surname"
              defaultValue={attribute.data['surname']}
              onChange={debouncedChange}
            />
          </div>
        </div>
      );
      break;
    case AttributeDefinitions.InstagramUsername.type.toString():
    case AttributeDefinitions.TiktokUsername.type.toString():
    case AttributeDefinitions.TwitterUsername.type.toString():
    case AttributeDefinitions.LinkedInUsername.type.toString():
    case AttributeDefinitions.FacebookUsername.type.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="handle">{attribute.typeDefinition.name}</label>
            <Input
              id="handle"
              name={attribute.typeDefinition.name.toLowerCase()}
              defaultValue={attribute.data[attribute.typeDefinition.name.toLowerCase()]}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.FullBio.type.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="short-bio">{t('Bio')}</label>
            <Input
              id="short-bio"
              name="short_bio"
              defaultValue={attribute.data['short_bio']}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <label htmlFor="full-bio">{t('Full bio')}</label>
            <Textarea
              id="full-bio"
              name="full_bio"
              defaultValue={attribute.data['full_bio']}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.CreditCard.type.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="cc-alias">{t('Alias')}</label>
            <Input
              id="cc-alias"
              name="cc_alias"
              defaultValue={attribute.data['cc_alias']}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <label htmlFor="cc-name">{t('Name on Card')}</label>
            <Input
              id="cc-name"
              name="cc_name"
              defaultValue={attribute.data['cc_name']}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <label htmlFor="cc-number">{t('Credit card number')}</label>
            <Input
              id="cc-number"
              name="cc_number"
              defaultValue={attribute.data['cc_number']}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    default:
      return (
        <>
          {Object.keys(attribute.data).map((dataKey) => {
            return (
              <p className="whitespace-pre-line" key={dataKey}>
                {dataKey}: {attribute.data[dataKey]}
              </p>
            );
          })}
        </>
      );
  }
};

export default AttributeFields;
