import { ProfileDefinition, ProfileSection } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { useParams } from 'react-router-dom';

import useAttributes, { AttributeVm } from '../../../hooks/profiles/useAttributes';
import useProfiles from '../../../hooks/profiles/useProfiles';

import TabMenu from '../../TabMenu/TabMenu';
import AttributeEditor from './Editors/AttributeEditor';
import AttributeCreator from './Editors/AttributeCreator';
import Section from '../../Sections/Section';
import Input from '../../Form/Input';
import ActionButton from '../../Buttons/ActionButton';
import Add from '../../Icons/Plus/Plus';
import { t } from '../../../helpers/i18n/dictionary';
import Collapse from '../../Icons/Colapse/Collapse';
import PageMeta from '../../Layout/PageMeta/PageMeta';

const Profile = () => {
  const { data, isLoading } = useProfiles();
  const { profileKey } = useParams();

  const profileDef = data?.definitions.find((curr) => {
    return curr.slug === profileKey;
  });

  const [activeSectionKey, setActiveSectionKey] = useState<string>(
    profileDef?.sections ? profileDef.sections[0].sectionId : ''
  );

  if (isLoading) {
    return <>Loading</>;
  }
  if (!data) {
    return <>{t('no-data-found')}</>;
  }

  if (!profileDef) {
    return <>Incorrect profile path</>;
  }

  const activeSection =
    activeSectionKey === 'new'
      ? undefined
      : profileDef.sections.find((sect) => {
          return sect.sectionId === activeSectionKey;
        }) || profileDef.sections[0];

  return (
    <>
      <PageMeta title={profileDef.name} />
      <TabMenu
        className="mt-5"
        items={[
          ...profileDef.sections.map((sect, index) => {
            return {
              title: sect.name,
              key: sect.sectionId,
              isActive: activeSectionKey ? activeSectionKey === sect.sectionId : index === 0,
            };
          }),
          {
            title: <Add className="h-5 w-5" />,
            key: 'new',
            isActive: activeSectionKey ? activeSectionKey === 'new' : !profileDef.sections.length,
            className: 'flex-grow-0',
          },
        ]}
        onChange={(key) => {
          setActiveSectionKey(key);
        }}
      />
      {activeSectionKey === 'new' ? (
        <ProfileSectionCreator profileDefinition={profileDef} />
      ) : (
        activeSection && (
          <ProfileSectionEditor
            section={activeSection}
            profileId={profileDef.profileId}
            key={activeSection.sectionId}
          />
        )
      )}
    </>
  );
};

const ProfileSectionCreator = ({ profileDefinition }: { profileDefinition: ProfileDefinition }) => {
  const [name, setName] = useState('');

  const createSection: React.FormEventHandler<HTMLFormElement> = (e) => {
    e.preventDefault();

    const newProfileSection = {
      sectionId: '',
      attributes: [],
      priority: Math.max(...profileDefinition.sections.map((sect) => sect.priority)) + 1,
      isSystemSection: false,
      name: name,
    };

    const newProfileDef: ProfileDefinition = { ...profileDefinition };
    newProfileDef.sections.push(newProfileSection);

    console.log('Should create: ', newProfileDef);

    return false;
  };

  return (
    <Section title="New: section" isOpaqueBg={true}>
      <form onSubmit={createSection}>
        <div className="mb-5">
          <label htmlFor="name">{t('Name')}</label>
          <Input
            id="name"
            name="sectionName"
            onChange={(e) => {
              setName(e.target.value);
            }}
            required
          />
        </div>
        <div className="flex flex-row">
          <ActionButton className="ml-auto">{t('add section')}</ActionButton>
        </div>
      </form>
    </Section>
  );
};

const ProfileSectionEditor = ({
  section,
  profileId,
}: {
  section: ProfileSection;
  profileId: string;
}) => {
  const [{ data: attributes, isLoading }] = useAttributes({
    profileId: profileId,
    sectionId: section.sectionId,
  });

  if (!attributes || isLoading) {
    return <>Loading</>;
  }

  // Find unique types
  const types: string[] = attributes.reduce((prevVal, curVal) => {
    if (prevVal.indexOf(curVal.type) !== -1) {
      return prevVal;
    }
    return [...prevVal, curVal.type];
  }, [] as string[]);

  // Find matching attributes for those types
  const groupedAttributes = types.map((currType) => {
    const matchingAttributes = attributes.filter((attr) => attr.type === currType);

    return { name: matchingAttributes[0].typeDefinition.name, attributes: matchingAttributes };
  });

  return (
    <>
      {attributes.length ? (
        groupedAttributes.map((attrGroup) => {
          return (
            <AttributeGroup
              groupTitle={attrGroup.name}
              attributes={attrGroup.attributes}
              key={attrGroup.name}
            />
          );
        })
      ) : (
        <div className="py-5">{t('section-empty-attributes')}</div>
      )}
      <AttributeCreator profileId={profileId} sectionId={section.sectionId} />
    </>
  );
};

const AttributeGroup = ({
  attributes,
  groupTitle,
}: {
  attributes: AttributeVm[];
  groupTitle: string;
}) => {
  const [isActive, setIsActive] = useState(attributes.length === 1);

  if (attributes.length === 1) {
    return <AttributeEditor attribute={attributes[0]} />;
  }

  return (
    <div
      className={`relative my-10 overflow-x-hidden ${
        !isActive ? 'cursor-pointer transition-transform' : ''
      }`}
      style={{ paddingBottom: `${attributes.length * 10}px` }}
      onClick={() => {
        if (!isActive) setIsActive(true);
      }}
    >
      <h2 onClick={() => setIsActive(false)} className={'cursor-pointer text-2xl'}>
        <Collapse className={`inline-block h-4 w-4 ${isActive ? 'opacity-100' : 'opacity-0'}`} />{' '}
        {groupTitle} <small>({attributes.length})</small>
      </h2>
      <div
        className={`border-l-[16px] border-slate-50 pt-5 transition-transform dark:border-slate-600 ${
          isActive ? 'pl-5' : '-translate-x-4 hover:translate-x-0'
        }`}
      >
        {attributes.map((attr, index) => {
          return (
            <span
              key={attr.id}
              className={
                !isActive && index !== 0
                  ? `absolute left-0 right-0 top-0 bg-white shadow-slate-50 dark:bg-slate-800`
                  : ''
              }
              style={{ transform: `translateX(${index * 4}px) translateY(${index * 10}px)` }}
            >
              <AttributeEditor
                attribute={attr}
                className={
                  !isActive ? 'pointer-events-none my-0 opacity-50 grayscale' : 'mt-0 mb-5'
                }
              />
            </span>
          );
        })}
      </div>
    </div>
  );
};

export default Profile;
